<!--  -->
<template>
  <div class="getContainer">
    <el-tabs type="border-card">
      <el-tab-pane label="Params">
        <el-table
          height='200'
          :data="paramsTable"
          style="width: 100%">
          <el-table-column
            label="KEY"
            align="center"
            width="">
            <template slot-scope="props">
              <el-input 
                @input="handelChange('paramsTable',props.row,props.$index)"
                size="mini"
                v-model="props.row.paramsKey" 
                placeholder="请输入内容"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            label="VALUE"
            align="center"
            width="">
            <template slot-scope="props">
              <el-input 
                @input="handelChange('paramsTable',props.row,props.$index)"
                size="mini"
                v-model="props.row.paramsValue" 
                placeholder="请输入内容"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            width="80">
            <template slot-scope="props">
              <el-button size="mini" round @click="handelDle('paramsTable',props.row,props.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="Headers">
        <el-table
          height='200'
          key="getHeader"
          :data="headersTable"
          style="width: 100%">
          <el-table-column
            label="KEY"
            align="center"
            width="">
            <template slot-scope="props">
              <el-input 
                @input="handelChange('headersTable',props.row,props.$index)"
                size="mini"
                v-model="props.row.headerKey" 
                placeholder="请输入内容"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            label="VALUE"
            align="center"
            width="">
            <template slot-scope="props">
              <el-input 
                @input="handelChange('headersTable',props.row,props.$index)"
                size="mini"
                v-model="props.row.headerValue" 
                placeholder="请输入内容"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            align="center"
            width="80">
            <template slot-scope="props">
              <el-button size="mini" round @click="handelDle('paramsTable',props.row,props.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="Body" v-if="IFGET">
        <div class="contentTypeContent">
          <el-radio-group v-model="contentType" @change="handelContentType">
            <el-radio label="json">json</el-radio>
            <el-radio label="form-data">form-data</el-radio>
            <el-radio label="x-www-form-urlencoded">x-www-form-urlencoded</el-radio>
          </el-radio-group>
        </div>
        <el-table
          height='200'
          key="getHeader"
          :data="bodyTable"
          style="width: 100%">
          <el-table-column
            label="KEY"
            align="center"
            width="">
            <template slot-scope="props">
              <el-input 
                @input="handelChange('bodyTable',props.row,props.$index)"
                size="mini"
                v-model="props.row.bodyKey" 
                placeholder="请输入内容"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            label="VALUE"
            align="center"
            width="">
            <template slot-scope="props">
              <el-input 
                @input="handelChange('bodyTable',props.row,props.$index)"
                size="mini"
                v-model="props.row.bodyValue" 
                placeholder="请输入内容"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            align="center"
            width="80">
            <template slot-scope="props">
              <el-button size="mini" round @click="handelDle('paramsTable',props.row,props.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 上传文件 -->
        <div class="fileTitle" v-if="isFormData">是否上传文件</div>
        <div class="fileContainer" v-if="isFormData">
          <div>
            <el-switch
              size="mini"
              @change="handelSwitch"
              v-model="fileSwitch"
              active-color="#13ce66"
              inactive-color="#ff4949">
            </el-switch>
          </div>
          <div class="fileNameContent">
            <el-input 
              :disabled="!fileSwitch"
              size="mini"
              v-model="fileName" 
              placeholder="请输入内容"></el-input>
          </div>
          <div>
            <el-upload
              class="upload-demo"
              ref="upload"
              action="https://jsonplaceholder.typicode.com/posts/"
              :auto-upload="false">
              <el-button slot="trigger" size="mini" type="primary" :disabled="!fileSwitch">选取文件</el-button>
            </el-upload>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>


  </div>
</template>

<script>
export default {
  name:"getComponent",
  props:{
    paramsData:{
      default:()=>{
        return []
      }
    },
    IFGET:{
      type:Boolean,
      default:false
    }
  },
  data () {
    return {
      contentType:'json',
      isFormData:false,
      fileSwitch:false,
      paramsTable:[
        {
          paramsKey:"",
          paramsValue:""
        }
      ],
      headersTable:[
        {
          headerKey:"",
          headerValue:"",
        }
      ],
      bodyTable:[
        {
          bodyKey:"",
          bodyValue:"",
        }
      ],
      // 文件参数
      fileName:"",
      

    };
  },
  
  watch:{
    paramsData(newValue){
      newValue.forEach((item,index)=>{
        if(this.paramsTable.length-1 < index){
          this.paramsTable.push({
            paramsKey:"",
            paramsValue:"",
          })
        }
        this.$set(this.paramsTable[index],'paramsKey',item.key)
        this.$set(this.paramsTable[index],'paramsValue',item.value)
      })
    }
  },

  components:{},

  computed:{},

  mounted(){},

  methods:{
    // 输入框创建
    handelChange(dataName,row,index){
      if(this[dataName].length-1 === index){
        this[dataName].push({
          paramsKey:"",
          paramsValue:""
        })
      }
      if(dataName === "paramsTable"){
        this.$emit("paramTableCallBack",this[dataName])
      }
    },
    // 输入框删除
    handelDle(dataName,row,index){
      console.log("index",index)
      if(index > 0){
        this[dataName].splice(index,1)
      }
    },
    // 监听post请求格式
    handelContentType(){
      console.log("post文件格式",this.contentType)
      if(this.contentType === "form-data"){
        this.isFormData = true
      }else{
        this.isFormData = false
      }
    },
    // 监听文件上传开关
    handelSwitch(){
      console.log("文件开关",this.fileSwitch)
    }

  }
}

</script>
<style scoped>
.getContainer /deep/ .el-tabs__content{
  padding-top:0px
}
.contentTypeContent{
  margin-top: 6px;
}
.fileTitle{
  margin-top: 10px;
  font-size: 12px;
}
.fileContainer{
  margin-top: 10px;
  display: flex;
  align-items: center;
}
.fileContainer>div{
  margin-right: 10px;
}
</style>