<template>
  <div class="home">
    <header class="header">
      <div class="methodsContent">
        <el-select v-model="methods" placeholder="请选择" @change="handelMethods">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.value"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="urlContent">
        <el-input v-model="APIurl" placeholder="request URL" @input="handelURL"></el-input>
      </div>
      <div class="sendContent">
        <el-button type="primary">send</el-button>
      </div>
    </header>
    <!-- 配置项 -->
    <div class="configContainer">
      <Get 
        :IFGET="IFGET"
        :paramsData="getParams" 
        @paramTableCallBack="paramTableCallBack"></Get>
    </div>
    <!-- response -->
    <div class="configContainer">
      <Response></Response>
    </div>
    



  </div>
</template>

<script>
import Get from "../components/get.vue"
// import Post from "../components/post.vue"
import Response from "../components/response.vue"
export default {
  name: 'HomeView',
  components:{Get,Response},
  data(){
    return {
      IFGET:false,
      APIurl:"",
      methods:"GET",
      options:[
        {
          value:"POST"
        },
        {
          value:"GET"
        }
      ],
      getParams:[],
    }
  },
  methods:{
    handelMethods(){
      this.IFGET = !this.IFGET
    },
    handelURL(){
      let  urlObj = ""
      try {
        urlObj = new URL(this.APIurl);
      } catch (err) {
        return
      }
      const urlSearchParams = urlObj.searchParams;
      const paramObj = Object.fromEntries(urlSearchParams);
      let paramsArr = []
      if(JSON.stringify(paramObj) !== '{}'){
        for(let key in paramObj){
          paramsArr.push({
            key:key,
            value:paramObj[key]
          })
        }
        this.getParams = paramsArr
      }

    },
    // 回调的URL
    paramTableCallBack(data){
      let index = this.APIurl.indexOf("?")
      let apiUrl = "";
      if(index !== -1){
        apiUrl = this.APIurl.substring(0,index)
      }else{
        apiUrl = this.APIurl
      }
      let urlParams = data.map((item,index)=>{
        if(item.paramsKey !== ""){
          console.log("xxx")
          if(index === 0){
            return `?${item.paramsKey}=${item.paramsValue}`
          }else{
            return `&${item.paramsKey}=${item.paramsValue}`
          }
        }else{return ""}
      })
      this.APIurl = apiUrl+urlParams.join("")
    },



  }
}
</script>

